import React, { useEffect } from 'react';
import { CalendarComponent } from '@syncfusion/ej2-react-calendars';
import Header from './header'
import MainFooter from './main_footer'
import Clock from './clock'
import {connect} from 'react-redux';

import './reset.css';
import './calendar.scss'

function Calendar(props) {
    let li = [1,2,3]
    return(
        <div>
        <div className="wrap calendar">
         <header>
             <Clock></Clock>
            <Header></Header>
         </header>
         <CalendarComponent></CalendarComponent>
        <section className="important_data">
            <div className="title_wrap">
            <div className="date_title">예약된 일정</div>
            </div>
            {
                li.map(function (a,i) {
                    return <DateWrap i={i}></DateWrap>

                })
            }
            <button>디데이설정</button>
        </section>
        </div>
        <MainFooter></MainFooter>
        </div>
    )
}

function DateWrap(props) {
    return(
        <div>
        <div className="date_wrap" id={`li${props.i}`}>
            <div className="date_txt">
                <p>완벽한 하루 끝내기</p>
            </div>
            <div className="date_btn">
                 <img src="/img/Radio_Button.svg" alt="라디오"></img>
            </div>
        </div>
        </div>
    )
}

  export default Calendar;